import {makeAutoObservable} from 'mobx'
import services from "../services/services";
import {DataLoan} from "../models/dataLoan";

interface ILoansStateStore {
    group: string
    details: string
    name: string
    search: string
    stateOffcanvas: boolean
    stateLoader: boolean
    messageToastHome: string
    technicalWorks: boolean
    date: string
    dataLoan: DataLoan[]
    topLoans: DataLoan[]
    bottomBtn: number
    placeholderSearch: string
    homeDropdown: string
}

class LoansStateStore implements ILoansStateStore {
    group = ''
    details = ''
    name = ''
    search = ''
    date = ''
    homeDropdown = ''
    placeholderSearch = ` `
    messageToastHome = ''
    dataLoan: DataLoan[] = []
    topLoans: DataLoan[] = []
    stateOffcanvas = false
    stateLoader = false
    bottomBtn = null
    technicalWorks = false

    constructor() {
        makeAutoObservable(this)
    }

    setSearch(val: string): void {
        this.search = val
    }

    setHomeDropdown(val: string): void {
        this.homeDropdown = val
    }

    setDetails(val: string): void {
        this.details = val
    }

    setPlaceholderSearch(val: string): void {
        this.placeholderSearch = val
    }


    setTechnicalWorks(val: boolean): void {
        this.technicalWorks = val
    }

    setBottomBtn(val: number): void {
        this.bottomBtn = val
    }

    setStateLoader(val: boolean): void {
        this.stateLoader = val
    }


    setDataLoan(val: DataLoan[]): void {
        this.dataLoan = val
    }

    setTopLoans(val: DataLoan[]): void {
        this.topLoans = val
    }

    userIP(): void {
        services
            .getIp()
            .then((res) => {
                this.setDetails(res)
            })

    }

    dataLoans(): void {
        services
            .getLoans()
            .then((res) => {
                res.loanDetails.sort((a, b) => {
                        return a.place - b.place
                    }
                )
                this.setDataLoan(res.loanDetails)
                this.setTechnicalWorks(res.technicalWorks)
                this.setTopLoans(res.topLoans)
            })

    }

    statusBtn(): void {
        services
            .getStatusBtn()
            .then((res) => {
                this.setTechnicalWorks(res)
            })

    }
}

export default new LoansStateStore()
