/* eslint-disable @typescript-eslint/no-unsafe-member-access,  @typescript-eslint/no-unsafe-call*/

import axios from 'axios'


import {DataApi} from "../models/dataApi";
import {toast} from "react-toastify";
import Toaster from "../../../core/lib/toaster/toaster";
import loansStateStore from "../store/loans-state-store";


export default {
    async getIp() {
        loansStateStore.setStateLoader(true)
        let result: string = ''
        const url = 'https://ipgeolocation.abstractapi.com/v1/?api_key=af772993d8ce4dada5690c0645f83e7e'
        await axios
            .get(url)
            .then((res) => {
                result = res.data.country_code

            })
            .catch(() => {
                new Toaster({msg: '  ', type: toast.TYPE.ERROR})
            })
            .then(() => {
                loansStateStore.setStateLoader(false)
                new Toaster({msg: ' ', type: toast.TYPE.SUCCESS})
            })

        return result
    },
    async getLoans(): Promise<DataApi> {
        loansStateStore.setStateLoader(true)
        let result: DataApi

        const url = 'https://functions.yandexcloud.net/d4ev12190l7vr3ql70j0'

        await axios
            .get(url)
            .then((res) => {
                result = res.data
            })
            .catch(() => {
                    new Toaster({msg: ',   ', type: toast.TYPE.ERROR})
                }
            )
            .then(() => {
                loansStateStore.setStateLoader(false)
                new Toaster({msg: ' ', type: toast.TYPE.SUCCESS})
            })
        // @ts-ignore
        return result
    },
    async getStatusBtn() {
        loansStateStore.setStateLoader(true)
        let result: boolean

        const url = 'https://functions.yandexcloud.net/d4eppsl8tu75dffiqb40'

        await axios
            .get(url)
            .then((res) => {
                result = res.data.secondApp
            })
            .catch(() => {
                }
            )
            .then(() => {
                loansStateStore.setStateLoader(false)
            })

        return result
    },

}
