import React from 'react'
import {observer} from 'mobx-react-lite'
import './BottomApp.scss'
import routes from "../../../app/lib/routes";
import loansStateStore from "../../../app/lib/store/loans-state-store";
import {faCircleInfo, faHome} from "@fortawesome/free-solid-svg-icons";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {nanoid} from "nanoid";


export default observer(() => {

    return (
        <div key={nanoid()} className="btm-nav">
            <a key={nanoid()} href={'#' + routes.HOME}   className={loansStateStore.bottomBtn ===0  && "active"}>
                <FontAwesomeIcon key={nanoid()} icon={faHome}/>
            </a>
            <a key={nanoid()} href={'#' + routes.INFO}   className={loansStateStore.bottomBtn === 2 && "active"}>
                <FontAwesomeIcon key={nanoid()} icon={faCircleInfo}/>
            </a>
        </div>
    )
})
