import React, {useState} from 'react'
import {observer} from 'mobx-react-lite'
import './SearchForm.scss'
import loansStateStore from "../../../lib/store/loans-state-store";
import CustomCard from "../Cards/CustomCard";
import {nanoid} from "nanoid";



export default observer(() => {
    const [valueSearch, seValSearch] = useState('')
    const filterVal = loansStateStore.dataLoan.filter((val) => {
        return val.title.toLowerCase().includes(valueSearch.toLowerCase())
    })

    return (
        <div className={'search-form'}>
            <input value={valueSearch} onChange={(e) => seValSearch(e.target.value)}
                   type="text" placeholder={loansStateStore.placeholderSearch}
                   className={`input bg-base-200 w-full search-form-input`}/>
            <div className={'flex flex-wrap search-form-body'}>
                {
                    filterVal.map((data) => (
                        data.place < 15 &&
                        <CustomCard key={nanoid()} data={data}/>
                    ))
                }
            </div>
        </div>
    )
})
