import React from 'react'
import {observer} from 'mobx-react-lite'
import './TopLoans.scss'
import loansStateStore from "../../../lib/store/loans-state-store";
import {passControl} from "../../../lib/helper";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faChevronRight, faTrophy} from "@fortawesome/free-solid-svg-icons";
import {toNowDay} from "../../../../core/date";


export default observer(() => {


    return (
        <div>
            <div className={'top'}>
                <FontAwesomeIcon icon={faTrophy} style={{color: '#3390ec'}}/>  
            </div>
            <br/>
            <div className="btn-group btn-group-vertical top-loans">
                {
                    loansStateStore.topLoans.map((data) => (
                        <a href={passControl() ? null
                            :
                            data.link}
                           onClick={
                               passControl() ?
                                   null
                                   :
                                   () => {

                                       loansStateStore.setStateLoader(true)
                                   }
                           }
                           className="btn btn-group-top bg-base-200 btn-ghost">
                            <div className="top-loans-name">
                                <div className="avatar">
                                    <div className="rounded-full w-8">
                                        <img alt={'12'} src={data.img}/>
                                    </div>
                                </div>
                                {data.title}
                            </div>
                            <FontAwesomeIcon icon={faChevronRight}/>
                        </a>
                    ))
                }
            </div>
            <div className=" top-loans-time">
                  {toNowDay()}
            </div>
        </div>
    )
})
