import React from 'react'
import {observer} from "mobx-react-lite";
import loansStateStore from "../../../app/lib/store/loans-state-store";
import {nanoid} from "nanoid";






export default observer(() => {


    return (
        <div key={nanoid()} >

            <input key={nanoid()} checked={loansStateStore.stateLoader} type="checkbox" id="my-modal" className="modal-toggle" />
            <div key={nanoid()} className="modal">
                <div key={nanoid()} className="modal-box">
                    <progress key={nanoid()} className="progress "></progress>
                </div>
            </div>
        </div>
    )
})
